.slider {
    overflow: hidden;
    position: relative;
    height: 100%;
    color: #fff;
}
.slider__top-heading {
    z-index: 12;
    position: absolute;
    left: 0;
    top: 100px;
    width: 100%;
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    transition: all 0.5s 1s;
    transform: translateY(-30px);
    opacity: 0;
}
.slider.slider-ready .slider__top-heading {
    transform: translateY(0);
    opacity: 1;
}
.slider-slides {
    position: relative;
    height: 100%;
}
.slider-slide {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}
.slider-slide.slider-active {
    pointer-events: auto;
}
.slider-slide-content {
    z-index: 6;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    text-transform: uppercase;
    line-height: 1;
}
.slider-slide-subheading {
    margin-bottom: 20px;
    font-size: 24px;
    letter-spacing: 2px;
    transform: translateY(20px);
    opacity: 0;
    transition: 0.5s;
}
.slider-slide.slider-active .slider-slide-subheading {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}
.slider-slide-heading {
    display: flex;
    margin-bottom: 20px;
    font-size: 60px;
    letter-spacing: 12px;
}
.slider-slide-heading span {
    display: block;
    opacity: 0;
    transform: translateY(-60px);
    transition: all 0.33333s;
}
.slider-slide.s--prev .slider-slide-heading span {
    transform: translateY(60px);
}
.slider-slide.slider-active .slider-slide-heading span {
    opacity: 1;
    transform: translateY(0);
}
.slider-slide-heading span:nth-child(1) {
    transition-delay: 0s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(1) {
    transition-delay: 0.33333s;
}
.slider-slide-heading span:nth-child(2) {
    transition-delay: 0.1s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(2) {
    transition-delay: 0.43333s;
}
.slider-slide-heading span:nth-child(3) {
    transition-delay: 0.2s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(3) {
    transition-delay: 0.53333s;
}
.slider-slide-heading span:nth-child(4) {
    transition-delay: 0.3s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(4) {
    transition-delay: 0.63333s;
}
.slider-slide-heading span:nth-child(5) {
    transition-delay: 0.4s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(5) {
    transition-delay: 0.73333s;
}
.slider-slide-heading span:nth-child(6) {
    transition-delay: 0.5s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(6) {
    transition-delay: 0.83333s;
}
.slider-slide-heading span:nth-child(n+7) {
    transition-delay: 0.6s;
}
.slider-slide.slider-active .slider-slide-heading span:nth-child(n+7) {
    transition-delay: 0.93333s;
}
.slider-slide-readmore {
    position: relative;
    font-size: 14px;
    text-transform: lowercase;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transform: translateY(-20px);
    cursor: pointer;
    opacity: 0;
    transition: 0.5s;
}
.slider-slide.slider-active .slider-slide-readmore {
    transition-delay: 0.65s;
    opacity: 1;
    transform: translateY(0);
}
.slider-slide-readmore:before {
    content: '';
    position: absolute;
    left: -2px;
    top: -3px;
    width: calc(100% + 4px);
    height: calc(100% + 6px);
    background: rgba(255, 255, 255, 0.4);
    transform: scaleX(0.3);
    transform-origin: 0 50%;
    transition: transform 0.3s;
}
.slider-slide-readmore:hover:before {
    transform: scaleX(1);
}
.slider-slide-parts {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    width: 100%;
    height: 100%;
}
.slider-slide-parts:after {
    content: '';
    z-index: 5;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
}
.slider-slide-part {
    position: relative;
    width: 25%;
    height: 100%;
}
.slider-slide-part-inner {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 100%;
    background-size: 0 0;
    background-repeat: no-repeat;
    transition: transform 0.5s ease-in-out;
}
.slider-slide-part-inner:before {
    content: '';
    position: absolute;
    width: 100vw;
    height: 100%;
    background-image: inherit;
    background-size: cover;
    background-position: center center;
    transition: opacity 0.25s;
    opacity: 0;
}
.slider-slide-part:nth-child(1) .slider-slide-part-inner {
    z-index: 3;
    transition-delay: 0.24s;
    transform: translateX(-32.5%);
}
.slider-slide.slider-active .slider-slide-part:nth-child(1) .slider-slide-part-inner {
    transition-delay: 0.28s;
}
.slider-slide-part:nth-child(1) .slider-slide-part-inner:before {
    left: 0vw;
    transition-delay: 0.365s;
}
.slider-slide.slider-active .slider-slide-part:nth-child(1) .slider-slide-part-inner:before {
    transition-delay: 0.28s;
}
.slider-slide-part:nth-child(2) .slider-slide-part-inner {
    z-index: 2;
    transition-delay: 0.16s;
    transform: translateX(-65%);
}
.slider-slide.slider-active .slider-slide-part:nth-child(2) .slider-slide-part-inner {
    transition-delay: 0.36s;
}
.slider-slide-part:nth-child(2) .slider-slide-part-inner:before {
    left: -25vw;
    transition-delay: 0.285s;
}
.slider-slide.slider-active .slider-slide-part:nth-child(2) .slider-slide-part-inner:before {
    transition-delay: 0.36s;
}
.slider-slide-part:nth-child(3) .slider-slide-part-inner {
    z-index: 1;
    transition-delay: 0.08s;
    transform: translateX(-97.5%);
}
.slider-slide.slider-active .slider-slide-part:nth-child(3) .slider-slide-part-inner {
    transition-delay: 0.44s;
}
.slider-slide-part:nth-child(3) .slider-slide-part-inner:before {
    left: -50vw;
    transition-delay: 0.205s;
}
.slider-slide.slider-active .slider-slide-part:nth-child(3) .slider-slide-part-inner:before {
    transition-delay: 0.44s;
}
.slider-slide-part:nth-child(4) .slider-slide-part-inner {
    z-index: 0;
    transition-delay: 0s;
    transform: translateX(-130%);
}
.slider-slide.slider-active .slider-slide-part:nth-child(4) .slider-slide-part-inner {
    transition-delay: 0.52s;
}
.slider-slide-part:nth-child(4) .slider-slide-part-inner:before {
    left: -75vw;
    transition-delay: 0.125s;
}
.slider-slide.slider-active .slider-slide-part:nth-child(4) .slider-slide-part-inner:before {
    transition-delay: 0.52s;
}
.slider-slide.slider-active .slider-slide-part-inner {
    transform: translateX(0);
    transition-timing-function: ease;
}
.slider-slide.slider-active .slider-slide-part-inner:before {
    opacity: 1;
}
.slider-control {
