.listbtn {
    width: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
    background-color: #fff0;
    border: none;
    outline: none;
}

.listbtn span {
    position: absolute;
    width: 1.2rem;
    height: .20rem;
    background: #e9e8e8;
    transition: all 0.3s linear;
}

    .listbtn span::before {
        content: "";
        position: absolute;
        width: 0;
        height: 100%;
        top: 0;
        right: 0;
        background: gray;
        transition: all 0.3s linear;
    }

    .listbtn span:nth-child(1) {
        -webkit-animation: span-first-off 0.5s ease-in-out;
                animation: span-first-off 0.5s ease-in-out;
        -webkit-animation-fill-mode: forwards;
                animation-fill-mode: forwards;
    }

    .listbtn span:nth-child(2) {
        -webkit-animation: span-second-off 0.5s ease-in-out;
                animation: span-second-off 0.5s ease-in-out;
        -webkit-animation-fill-mode: forwards;
                animation-fill-mode: forwards;
    }

    .listbtn span:nth-child(3) {
        -webkit-animation: span-third-off 0.5s ease-in-out;
                animation: span-third-off 0.5s ease-in-out;
        -webkit-animation-fill-mode: forwards;
                animation-fill-mode: forwards;
    }

.listbtn.on:hover span::before {
    width: 100%;
    transition: all 0.3s linear;
}

.listbtn.on span:nth-child(1) {
    -webkit-animation: span-first-on 0.5s ease-in-out;
            animation: span-first-on 0.5s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.listbtn.on span:nth-child(2) {
    -webkit-animation: span-second-on 0.5s ease-in-out;
            animation: span-second-on 0.5s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.listbtn.on span:nth-child(3) {
    -webkit-animation: span-third-on 0.5s ease-in-out;
            animation: span-third-on 0.5s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

