    height: 2rem;
    border: 1px solid #dfdfdf;
    border-color: #fff0 #dfdfdf #dfdfdf #fff0;
    background-color: #fff;
    position: relative;
    top: -1rem;
    transform: rotate(45deg); 
    -ms-transform: rotate(45deg); /* IE 9 */ 
    -webkit-transform: rotate(45deg); /* Safari and Chrome */
}
.pageedit-componentlistbox{
    z-index: 9999;
}

.pageedit-componentlistbox-frame {
    width: 170px;
    margin: 5px 5px;
    height: 110px;
    background-color: #fff0;
}

.pageedit-componentlistbox-header{
    background-color: #586675;
}

.pageedit-componentlistbox-body{
}

.pageedit-componentlistbox-close{
    border-radius: 50%;
}

.pageedit-componentlistbox-close>span:hover{
    color: gray;
}

.pageedit-componentlistbox-body>div{
    margin-bottom: 1rem;
}

.pageedit-componentlistbox-frame-active{
    border-color: #2393a7 !important;
}
.pageedit-component-tool {
    /* border: 1px solid #cdcdcd; */
    padding: 0rem;
    overflow-x: hidden;
    flex-shrink: 0;
    flex-grow: 0;
    width: 100%;
    height: 100%;
    margin-right: 0.33rem;
}

.pageedit-component-tool>div {
    height: 100%;
    overflow-y: scroll;
    margin-right: -1.5rem;
    padding: 0rem;
}

.pageedit-component-tool-frame {
    width: 100%;
    height: 6rem;
    background-color: #fff0;
}

.pageedit-component-tool-list {
    width: 100%;
    display: block;
    overflow: hidden;
    padding: 0rem;
}

.pageedit-component-tool-list>li,
.pageedit-component-tool-list a {
    list-style: none;
    text-decoration: none;
}

.pageedit-component-tool-item {
    overflow: hidden;
}

.pageedit-component-tool-item>a {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background-color: #586675;
    color: #fff;
    font-size: .9rem;
    position: relative;
}

.pageedit-component-tool-item>.subMenu {
    background: #273057;
    overflow: hidden;
    max-height: 0rem;
    transition: all 0.7s;
}

.pageedit-component-tool-item>.subMenu>a {
    display: block;
    padding: .5rem 1rem;
    color: #fff;
    /* border-bottom: 1px solid #394c7f; */
    position: relative;
}

.pageedit-component-tool-item>.subMenu>a:hover {
    background: #273057;
    background: linear-gradient(to bottom, #273057 0%, #273057 50%, #394c7f 51%, #394c7f 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#273057', endColorstr='#394c7f', GradientType=0);
    transition: all 0.3s;
    /* border-bottom: 1px solid #394c7f; */
}

.pageedit-component-tool-item>.subMenu>a:last-child {
    border: none;
}

.pageedit-component-tool-item>.subMenu-active {
    max-height: 40rem;
}
.postedit-page-container{
    height: 100%;
    overflow-x: hidden;
    padding: 0px;
    display: flex;
    flex-direction: column;
    flex-shrink: 1;
    flex-grow: 1;
}

.postedit-page-container-header{
    position: fixed;
    display: inline-block;
    z-index: 9999;
    bottom: 20vh;
    opacity: 0.7;
}

.postedit-page-container-header:hover{
    opacity: 1;
}
.parentcomponent .parentcomponent-btns {
    z-index: 990;
}

.parentcomponent .parentcomponent .parentcomponent-btns {
    z-index: 991;
}

.parentcomponent .parentcomponent .parentcomponent .parentcomponent-btns {
    z-index: 992;
}

.parentcomponent .parentcomponent .parentcomponent .parentcomponent .parentcomponent-btns {
    z-index: 993;
}

.parentcomponent .parentcomponent .parentcomponent .parentcomponent .parentcomponent .parentcomponent-btns {
    z-index: 994;
}

.parentcomponent:hover>.parentcomponent-btns>button {
    opacity: 1;
}

.parentcomponent-btns {
    width: 0px;
    left: -3rem;
    position: relative;
}

.parentcomponent-btns>button {
    opacity: 0.2;
    transition: 0.5s;
    -webkit-transition: 0.5s;
}

